.taskInput{
    width: 100%;
    font-size: 16px;
    height: 2rem;
    cursor: pointer;
    background-color: inherit;
    border-radius: 3px;
}

.taskLabel{
    width: 100%;
    font-size: 16px;
    cursor: pointer;
    background-color: inherit;
    text-align: left;
    overflow: hidden;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    display: -webkit-box;
}

.incPomidorButton{
    width: 25px;
    height: 25px;
    align-content: center;
    border: 1px solid var(--greyC4);
    border-radius: 100%;
}