.header{
    background-color: var(--greyC4);
    height: 55px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding-left: 40px;
    padding-right: 40px;
}

.area{
    background-color: #f4f4f4;
    display: flex;
    flex-direction: row;
    height: 450px;
}

.content{
    display:flex;
    flex-direction:column;
    gap:10px;
    width:100%;
    margin:auto;
}

.timeContainer {
    width: 500px;
}

.timeWithProlongateContainer {
    margin: auto;
    display: flex;
}

.stubContainer {
    width: 50px;
    display: flex;
}

.started{
    background-color: var(--light-green);
}

.pause{
    background-color: var(--red);
}

.buttonsArea{
    display: flex;
    flex-direction: row;
    gap: 10px;
    margin: auto;
}

.time{
    font-size: 150px;
    line-height: 179px;
    font-weight: 200;
    color: #333333;
}

.startedText{
    color: var(--light-green);
}

.pauseText{
    color: var(--red);
}

.prolongTimeButton {
    width: 50px;
    height: 50px;
    border-radius: 100%;
    border: none;
    background-color: #C4C4C4;
    color: white;
    margin: auto;
    padding-top: 2px;
}


.prolongTimeButton:hover{
    background-color: #899441;
}