.button {
    height: 55px;
    padding: 15px 50px;
    font-size: 16px;
    line-height: 17px;
    font-weight: 500;
}

.normal{
    background-color: #A8B64F;
    border: none;
    color: white;
}

.normal:hover{
    background-color: #899441;
}

.alarm{
    background-color: white;
    border: 1px solid var(--red);
    color: var(--red);
}

.alarm:hover{
    background-color: var(--red);
    color: white;
}

.disabled{
    background-color: var(--greyF4);
    color: var(--greyC4);
    border: 1px solid var(--greyC4);
}

.disabled:hover{
    background-color: var(--greyF4);
    color: var(--greyC4);
    border: 1px solid var(--greyC4);
}