.header{
    display: flex;
    justify-content: space-between;
    padding: 0 80px;
    height: 70px;
    align-items: center;
    background-color: var(--white);
}

.logoText{
    color: var(--red);
    font-size: 24px;
    line-height: 24px;
    font-weight: 300;
}

.logoText:hover{
    color: var(--dark-red);
}

.menuText{
    color: var(--red);
    font-size: 16px;
    line-height: 17px;
    font-weight: 400;
}

.menuText:hover{
    color: var(--dark-red);
}

.logoImage {
    width: 40px;
    height: 40px;
}

.link{
    text-decoration: none;
    user-select: none;
}

.menuContainer{
    color: var(--red);
    fill: var(--red);
    display:flex;
    flex-direction:row;
    gap:5px;
    align-items:center;
}

.menuContainer:hover {
    color: var(--dark-red);
    fill: var(--dark-red);
}