.menuButton {
    width: 370px;
    height: 55px;
    border: none;
    background-color: var(--greyF4);
    display: flex;
    justify-content: space-between;
    padding: 20px 15px;
    cursor:pointer;
    align-items: center;
}

.dropdown{
    background-color: var(--white);
    width: 370px;
}

.menuItem{
    text-align: left;
    padding: 20px 15px;
    border-top: 1px solid var(--greyDE);
    cursor: pointer;
}

.menuItem:hover{
    background-color: var(--greyF4);
}